function PlusMinus(props) {
  function handle(e) {
    if (e.target.id.includes('minus')) {
      props.handle({name:props.section, value: -1})
      return
    }
    props.handle({name:props.section, value: 1})
  }
// plus className="bi bi-plus-circle"
// minus className="bi bi-dash-circle"
  return (
    <>
      <span 
        className="bi bi-plus-circle-fill"
        id="plus"
        onClick={((e) => handle(e))}
        alt="Images plus" 
      ></span>
      <span
        className="bi bi-dash-circle-fill"
        id="minus"
        onClick={((e) => handle(e))}
        alt="Images minus" 
      ></span>
    </>
  )
}

export default PlusMinus
