import React from 'react';
import Header from './components/Header';
import Left from './components/Left';
import Article from './components/Article';
import Right from './components/Right';
import Footer from './components/Footer';
import './App.css';
const superagent = require('superagent')

const update = (section, value) => {
  return new Promise(( resolve, reject ) => {
    let url = `/update/${section}/${value}`
    superagent
      .get(url)
      .end(function(err, res) {
        err ? reject(null) : resolve(res.body)
      })
  })
}

const read = () => {
  return new Promise((resolve,reject) => {
    let url = '/data'
    superagent
      .get(url)
      .end(function(err, res){
        err ? reject(null) : resolve(res.body)
      })
  })
}

function App() {
  const [data, setData] = React.useState({header:0,left: 0,article: 0,right: 0,footer: 0})

  React.useEffect(()=>{
    //Read db data and update UI
    read()
      .then(res => {
        setData(res)
    })

  }, [])

  function handle(section) {
    update(section.name, section.value)
      .then(res => {
        setData(res)
    })
  }

  return (
    <div className='grid'>
      <Header handle={handle} data={data}/>
      <Left handle={handle} data={data}/>
      <Article handle={handle} data={data}/>
      <Right handle={handle} data={data}/>
      <Footer handle={handle} data={data}/>
    </div>
  )
}

export default App;
